import * as C from '../types';
import { get } from '../api';

export const getProjectAll = () => async (dispatch, getState, api) => {
	dispatch({
		type: C.FETCH_PROJECT_ALL,
	});
	get('/project/getAll')
		.then(({ data }) => {
			dispatch({
				type: C.FINISHED_FETCH_PROJECT_ALL,
				data,
			});
		})
		.catch((error) => {
			dispatch({
				type: C.ERROR_FETCH_PROJECT_ALL,
				error,
			});
		});
};
