import React, { Fragment, useEffect, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import Top from './top';
import Serices from './services';
import Numbers from './numbers';
import Domain from './domain';
import Technology from './technology';
import Clients from './clients';
import Projects from './projects';
import ContactForm from './form';

import { getHomeInfo } from 'actions/home';
import { getDomainAll } from 'actions/domain';
import { getTeamCount } from 'actions/team';
import { getTechMains } from 'actions/tech';
import { getClientAll } from 'actions/client';
import { getProject } from 'actions/project';

function Home() {
	const dispatch = useDispatch();

	const fetchHomeInfo = useCallback(() => {
		dispatch(getHomeInfo());
		dispatch(getTechMains());
		dispatch(getTeamCount());
		dispatch(getDomainAll());
		dispatch(getClientAll());
	}, [dispatch]);

	useEffect(() => {
		fetchHomeInfo();
	}, []);

	return (
		<Fragment>
			<Top />
			<Serices />
			<Numbers />
			<Domain />
			<Technology />
			<Clients />
			<Projects />
			<ContactForm />
		</Fragment>
	);
}

export default Home;
